p {
  -webkit-margin-before: 0.5em;
  -webkit-margin-after: 0.5em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}

h1 {
  margin-bottom: 20px;
  -webkit-margin-before: 0px;
  -webkit-margin-after: 0px;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}

h1, h2, body {
  font-family: Roboto,Helvetica Neue,Helvetica,Arial,sans-serif;
}

a {
  text-decoration: none;
}

footer {
  color: #a0a0a0;
  font-weight: lighter;
  font-size: 0.9rem;
}

footer a {
  color: #4077a5;
}

footer a:hover {
  text-decoration: underline;
}

/*App component css*/
.App {
  text-align: left;
  color: #002e5c;
}

.App-logo {
  width: 100px;
  height: 25px;
  padding-top: 5px;
}

.App-bc-logo {
  width: 160px;
  padding-top: 5px;
  position: fixed;
  left: 235px;
}

.App-logo-no-text {
  width: 80px;
  height: 60px;
}

.App-header {
  padding: 10px;
  text-align: left;
  background: #f6fcff;
}

.App-title {
  vertical-align: top;
  margin-left: 10px;
  font-size: 2em;
  font-weight: lighter;
  display: inline-block;
  color: #4077a5;
  margin-top: 0px;
}

.App-instructions {
  font-size: 1.25em;
  font-weight: lighter;
  display: inline-block;
  margin-right: 5px;
}

.App-direction_icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-left: 5px;
}

.App-intro {
  font-size: 1.2em;
  margin-left: 92px;
  margin-top: -25px;
  color: #4077a5;
  text-align: left;
  font-weight: lighter;
}

.App-content {
  margin-left: 10px;
  text-align: left;
  background: white;
}

.App-footer {
  position: fixed;
  left: 5px;
  bottom: 0;
  width: 100%;
  background-color: #f6fcff;
  z-index: -1000;
}

.App-cooperation, .App-cooperation-small {
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  position: fixed;
  margin-top: 8px;
  font-style: italic;
}

.App-open-source {
  float: right;
  margin-right: 10px;
  padding-bottom: 5px;
}

/*GarageContainer component css*/
.GarageContainer-logo {
  width: 150px;
  height: 38px;
}

.GarageContainer-bc-logo {
  text-align: left;
  display: inline-block;
  padding: 0;
  padding-top: 7px;
}

.GarageContainer-bc-logoTitle {
  font-size: 19px;
  letter-spacing: -1px;
  color: #013149;
  text-transform: uppercase;
  font-weight: 650;
  display: block;
}

.GarageContainer-bc-logoSubTitle {
  font-weight: 100;
  font-size: 15px;
  color: #428bca;
  display: block;
  margin-top: -5px;
}

.GarageContainer-bc-logoText {
  margin-top: 8px;
  vertical-align: top;
  display: inline-block;
}

.GarageContainer-data-labels {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: no-wrap;
      flex-wrap: no-wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-right: 10px;
  font-size: 1.1rem;
  color: #a0a0a0;
  margin-bottom: 2px;
  font-weight: lighter;
}

/*GarageCard component css*/
.GarageCard-card {
  padding: 10px;
  padding-bottom: 0px;
  font-size: 1.7em;
  border: 1px solid #e9e9e9;
  height: 44px;
  margin-bottom: 5px;
  margin-right: 10px;
  color: #3498db;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  cursor: pointer;
}

.GarageCard-card:hover {
  background: #f6fcff
}

.GarageCard-name-text {
  vertical-align: top;
}

.GarageCard-city-icon {
  width: 25px;
  height: 25px;
  padding-top: 4px;
  margin-right: 5px;
}

.GarageCard-bc-icon {
  width: 28px;
  margin-right: 5px;
}

.hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

@media (max-width: 365px) {
  .App-logo-no-text { width: 60px; height: 45px }
  .App-title { font-size: 1.5em }
  .App-intro { font-size: 1em; margin-left: 70px; margin-top: -20px }
}

@media (max-width: 278px) {
  .App-logo-no-text { margin-left: 38% }
  .App-title { margin-left: 10% }
  .App-intro { margin-top: 0px; margin-left: 5% }
}

@media (max-width: 390px) {
  .App-cooperation-small { display: inline-block }
  .App-cooperation { display: none }
  .App-bc-logo { left: 143px }
}

@media (min-width: 391px) {
  .App-cooperation { display: inline-block }
  .App-cooperation-small { display: none }
}

@media (max-width: 480px) {
  .App-open-source { display: none }
}

@media (min-width: 768px) {
  .App-content { width: 750px }
}

@media (max-width: 766px) {
  .App { width: 100% }
}

@media (max-height: 620px) {
  .GarageCard-card { height: 38px }
}



